import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import image1 from '../assets/image1.jpg'
import image2 from '../assets/image2.jpg'
import image3 from '../assets/image3.jpg'

import image1mob from '../assets/image1mob.jpg'
import image2mob from '../assets/image2mob.jpg'
import image3mob from '../assets/image3mob.jpg'

const Imgcarousel = () =>{
    const imageUrl_1 = window.innerWidth >= 650  ? image1 : image1mob;
    const imageUrl_2 = window.innerWidth >= 650  ? image2 : image2mob;
    const imageUrl_3 = window.innerWidth >= 650  ? image3 : image3mob;
    return (
        <Carousel infiniteLoop={true}  autoPlay={true} interval={3000} showArrows={true} showThumbs={false}>
            <div>
                <img src={imageUrl_1} className='carasolu-img' />
                {/* <p className="legend">Legend 1</p> */}
            </div>
            <div>
                <img src={imageUrl_2} className='carasolu-img' />
                {/* <p className="legend">Legend 2</p> */}
            </div>
            <div>
                <img src={imageUrl_3} className='carasolu-img' />
                {/* <p className="legend">Legend 3</p> */}
            </div>
        </Carousel>
    );
}

export default Imgcarousel;