import Imgcarousel from './Imgcarousel'
import Grid  from '@mui/material/Grid';
import './CarasoulConatiner.css'

const CarasoulContainer = () => {
    return (
        <Grid container>
            <Grid item xs={12} class="carasoul-conatiner">
                <Imgcarousel/>
            </Grid>
        </Grid>
    );
}

export default CarasoulContainer;